body {
	background: #232526;
    background: -webkit-gradient(linear, left bottom, left top, from(#414345), to(#232526));
    background: linear-gradient(to top, #414345, #232526);

	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
	color: blanchedalmond;
}

.header-color {
	background: #6441a5; 
	background: -webkit-gradient(
		linear,
		left top, right top,
		from(#63159f),
		to(#6441a5)
    ); 
	background: linear-gradient(
		to right,
		#63159f,
		#6441a5
    ); 
}

.landing-grid {
	background: #232526;
    background: -webkit-gradient(linear, left bottom, left top, from(#414345), to(#232526));
    background: linear-gradient(to top, #414345, #232526);

	text-align: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.avatar-img {
	height: 250px;
	margin-top: 5em;
	border-radius: 20px;
}

.banner-text {
	background-color: rgb(35, 37, 38);
	opacity: 0.8;
	width: 75%;
	margin: auto;
	border-radius: 10px;
}

.banner-text h1 {
	padding-left: 20px;
	padding-top: 20px;
	font-size: 66px;
	font-weight: bold;
	color: white;
}

.banner-text hr {
	border-top: thin solid white;
	width: 50%;
	margin: auto;
}

.banner-text p {
	color: white;
	padding: 1em;
	font-size: 20px;
}

.social-links {
	display: flex;
	justify-content: space-between;
	width: 25%;
	margin: auto;
}

.social-links i {
	color: white;
	font-size: 5em;
}

.projects-grid{
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: auto;
}

.contact-body{
    margin: auto;
    background: rgb(35, 37, 38);
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em;
}

.contact-grid{
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
}
